.vfx-gradient-anim {
	background: linear-gradient(90deg, #181d28, #1a2849, #1f3e86, #073141);
	background-size: 400% 400%;
	animation: gradient 0.5s ease infinite;
	/* height: 100vh; */
}

@keyframes gradient {
	0% {
		background-position: 0% 50%;
	}

	50% {
		background-position: 100% 50%;
	}

	100% {
		background-position: 0% 50%;
	}
}

.vfx-fadeout-2s {
	animation: fadeout 1s ease-out;
}

@keyframes fadeout {
	0% {
		opacity: 1;
		transform: translateY(0px);
	}

	50% {
		opacity: 0.9;
		transform: translateY(-16px);
	}

	100% {
		opacity: 0;
		transform: translateY(-16px);
	}
}

.linear-wipe {
	text-align: center;

	background: linear-gradient(to right, #FFF 20%, #FF0 40%, #FF0 60%, #FFF 80%);
	background-size: 200% auto;

	color: #000;
	background-clip: text;
	text-fill-color: transparent;
	-webkit-background-clip: text;
	-webkit-text-fill-color: transparent;

	animation: shine 1s linear infinite;

	@keyframes shine {
		to {
			background-position: 200% center;
		}
	}
}

@keyframes pulse-danger {
	0% {
		box-shadow: 0 0 5px #ff0000, 0 0 10px #ff0000, 0 0 15px #ff0000, 0 0 20px #ff0000;
	}

	50% {
		box-shadow: 0 0 20px #ff0000, 0 0 30px #ff0000, 0 0 40px #ff0000, 0 0 50px #ff0000;
	}

	100% {
		box-shadow: 0 0 5px #ff0000, 0 0 10px #ff0000, 0 0 15px #ff0000, 0 0 20px #ff0000;
	}
}

.neon-pulse-danger {
	background-color: red;
	animation: pulse-danger 0.5s infinite;
}

@keyframes pulse-critical {
	0% {
		box-shadow: 0 0 5px #ff7300, 0 0 10px #ff7300, 0 0 15px #ff7300, 0 0 20px #ff7300;
	}

	50% {
		box-shadow: 0 0 15px #ff7300, 0 0 25px #ff7300, 0 0 30px #ff7300, 0 0 35px #ff7300;
	}

	100% {
		box-shadow: 0 0 5px #ff7300, 0 0 10px #ff7300, 0 0 15px #ff7300, 0 0 20px #ff7300;
	}
}

.neon-pulse-critical {
	background-color: #ff7300;
	animation: pulse-critical 1s infinite;
}

@keyframes pulse-safe {
	0% {
		box-shadow: 0 0 5px #23d100, 0 0 10px #23d100, 0 0 15px #23d100, 0 0 20px #23d100;
	}

	50% {
		box-shadow: 0 0 15px #23d100, 0 0 25px #23d100, 0 0 30px #23d100, 0 0 35px #23d100;
	}

	100% {
		box-shadow: 0 0 5px #23d100, 0 0 10px #23d100, 0 0 15px #23d100, 0 0 20px #23d100;
	}
}

.neon-pulse-safe {
	background-color: #23d100;
	animation: pulse-safe 2s infinite;
}

@keyframes fadeInOut {
	0% {
		opacity: 0;
	}

	10% {
		opacity: 0;
	}

	20% {
		opacity: 1;
	}

	80% {
		opacity: 1;
	}
	
	90% {
		opacity: 0;
	}

	100% {
		opacity: 0;
	}
}

.fade-5 {
	animation: fadeInOut 5s infinite;
}